














































import { Vue, Component, Watch } from 'vue-property-decorator';
import { State, Action } from 'vuex-class';

@Component
export default class LinkClaim extends Vue {
  private timer: any = null;
  private phone: string = ''; // 
  private code: string = ''; // 
  private disabled: boolean = false;
  private show: boolean = false; // 
  private errMsg: string = ''; // 
  private btnText: string = ''; // 
  private bindPhoneSuccess: boolean = false; // 

  @State((state: any) => state.AccountModule.account) private account!: any;
  @State((state: any) => state.ResidenceModule.bed_detail) private bedDetail!: any;
  @Action('sendCaptchaMessage') private sendCaptchaMessage!: any;
  @Action('bindUserPhone') private bindUserPhone!: any;
  @Action('claimRoom') private claimRoom!: any;

  @Watch('show') private Show() {
    if (!this.show) {
      this.resetStatus();
    }
  }
  // 
  private handleClick(): void {
    if (this.account.user.phone === '') { // 
    // if (true) { // 
      this.show = true;
      this.bindPhoneSuccess = false;
    } else {
      this.handleClaimRoom();
    }
  }
  private bindPhoneBtn(): void {
    if (this.phone === '') {
      this.errMsg = '';
      return;
    } else if (this.code === '') {
      this.errMsg = '';
      return;
    } else if (this.testPhone()) {
      const that = this;
      this.bindPhone()
        .then(() => {
          this.handleClaimRoom();
        });
    }
  }
  // 
  private async bindPhone() {
    return new Promise((resolve, reject) => {
      this.bindUserPhone({
        data: {
          phone: this.phone,
          captcha: this.code,
          force: 0,
        },
        success: () => {
          if (resolve) {
            resolve();
          }
        },
        fail: (e: any) => {
          this.$dialog.alert({
            message: e,
          });
          if (reject) {
            reject();
          }
        },
      });
    });
  }
  // 
  private async handleClaimRoom() {
    return new Promise((resolve, reject) => {
      this.claimRoom({
        data: {
          id: this.bedDetail.room.id,
        },
        success: () => {
          this.show = true;
          this.bindPhoneSuccess = true;
          if (resolve) {
            resolve();
          }
        },
        fail: (e: any) => {
          this.$dialog.alert({
            message: e,
          });
        },
      });
    });
  }
  // 
  private captchaMessage(): void {
    if (this.testPhone()) {
      clearInterval(this.timer);
      this.disabled = true;
      let totalSecond: number = 60;
      this.btnText = `(${totalSecond})s`;
      this.timer = setInterval(() => {
        totalSecond--;
        if (totalSecond <= 0) {
          this.disabled = false;
          this.btnText = '';
          clearInterval(this.timer);
        } else {
          this.btnText = `(${totalSecond})s`;
        }
      }, 1000);
      this.sendCaptchaMessage({
        data: {
          phone: this.phone,
        },
        fail: (e: any) => {
          this.$dialog.alert({
            message: e,
          });
        },
      });
    }
  }
  // 
  private testPhone(): boolean {
    if (!(/^1[34578]\d{9}$/.test(this.phone))) {
      this.errMsg = '';
      return false;
    } else {
      this.errMsg = '';
      return true;
    }
  }
  // 
  private reload(): void {
    window.location.reload();
  }

  private changeShow(): void {
    this.show = !this.show;
  }

  private resetStatus(): void {
    this.phone = '';
    this.code = '',
    this.disabled = false;
    this.errMsg = '';
    this.btnText = '';
    this.bindPhoneSuccess = false;
    clearTimeout(this.timer);
  }
  //   
  private onBlur(e: any): void {
    document.body && (document.body.scrollTop = document.body.scrollTop);
  }
}
